import React, { useState, useRef, useEffect } from 'react';
import composeRefs from '@seznam/compose-react-refs';
import { useClipboard } from 'usable-react';
import { usePress } from 'react-aria';
import { TextField } from './text-field';
import { Icon, MonochromeIcons } from '../../icons';
import { HoverActivatedTooltip } from '../../feedback';
import { forwardRefWrapper } from '../../../libs/forward-ref-wrapper';
import { TextButton } from '../text-button/text-button';
import { mergeProps } from '../../../libs/merge-props';
import { AutoResizingDiv } from '../../layout';

interface CopyableTextFieldProps extends React.ComponentProps<typeof TextField> {
  onCopy?: () => void;
}

export const CopyableTextField = /* @__PURE__ */ forwardRefWrapper<HTMLInputElement, CopyableTextFieldProps>(
  'CopyableTextField',

  (props, externalRef) => {
    const inputRef = useRef<HTMLInputElement | null>(null);
    const [copyTooltipText, setCopyTooltipText] = useState('Copy');

    const { value, defaultValue, onCopy, size, ...otherProps } = props;

    useEffect(() => {
      if (copyTooltipText !== 'Copy') {
        const timeout = setTimeout(() => setCopyTooltipText('Copy'), 1000);
        return () => clearTimeout(timeout);
      }

      return undefined;
    }, [copyTooltipText]);

    const { copy, ref: clipboardRef } = useClipboard(() => {
      setCopyTooltipText('Copied!');
      if (onCopy) onCopy();
    });

    const { pressProps } = usePress({ onPress: copy });

    const suffix = (
      <HoverActivatedTooltip portalize placement="top">
        <HoverActivatedTooltip.Anchor>
          <TextButton size={size === 'lg' ? 'md' : size} {...mergeProps(pressProps)}>
            <Icon type={MonochromeIcons.Copy} style={{ lineHeight: 0 }} />
          </TextButton>
        </HoverActivatedTooltip.Anchor>

        <HoverActivatedTooltip.Content>
          <AutoResizingDiv>
            <p style={{ textAlign: 'center' }}>{copyTooltipText}</p>
          </AutoResizingDiv>
        </HoverActivatedTooltip.Content>
      </HoverActivatedTooltip>
    );

    return (
      <TextField
        value={value}
        defaultValue={defaultValue}
        suffix={suffix}
        size={size}
        {...otherProps}
        ref={composeRefs(inputRef, externalRef, clipboardRef)}
      />
    );
  },
);
